.App {
  font-family: 'Avenir', 'Helvetica', sans-serif;
  font-weight: 200;
  width: 100%;
  height: 100%;
  background-color: #151617;  
  
}

.App-header {
  position: fixed;
  top: 0;
  z-index: 9999;
  /* background-color: #151617; */
  width: 100%;
  height: 100px;
  color: white;
  /* box-shadow: 0 4px 4px 0 rgb(21, 22, 23); */
  -webkit-transition: background-color 0.5s;
  -o-transition: background-color 0.5s;
  transition: background-color 0.5s;
}

.clear{
  background-color: rgba(0, 0, 0, 0.5);
  /* backdrop-filter: blur(16px);
  -webkit-backdrop-filter: blur(16px); */
}

.App-logo{
  height: 100px;
}

.header-items{
  float: right;
  margin-right: 16px;
}

.header-item{
  font-size: 20pt;
  font-weight: 600;
  position: relative;
  cursor: pointer;
  line-height: 100px;
  padding-left: 32px;
  padding-right: 32px;
  -webkit-transition: opacity 0.3s;
  -o-transition: opacity 0.3s;
  transition: opacity 0.3s;
  float: left;
}

.header-item:active{
  -webkit-transition: none;
  -o-transition: none;
  transition: none;
  opacity: 0.5;
}

.header-item-line{
  position: absolute;
  width: 0;
  height: 3px;
  background-color: #ffffff;
  bottom: 24px;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  -webkit-transition: width 0.2s;
  -o-transition: width 0.2s;
  transition: width 0.2s;
}

.header-item:hover > .header-item-line{
  width: 64%;
}

.App-content{
  width: 100%;
}

.section{
  width: calc(100% - (16px * 2));  
  background: -webkit-gradient(linear, left top, left bottom, from(#151617), to(#0D1E25));  
  background: -webkit-linear-gradient(top, #151617, #0D1E25);  
  background: -o-linear-gradient(top, #151617, #0D1E25);  
  background: linear-gradient(180deg, #151617, #0D1E25);  
  padding: 16px;  
}

.highlighted{
  background: -webkit-linear-gradient(45deg, #CC800E, #DD45EB);
  background: -o-linear-gradient(45deg, #CC800E, #DD45EB);
  background: linear-gradient(45deg, #CC800E, #DD45EB);  
}

.hero{
  width: calc(100% - (15vw * 2));
  padding-left: 15vw;
  padding-right: 15vw;
  display: -ms-flexbox;
  display: flex;
}

.hero-section{
  width: 50%;
  float: left;
  z-index: 1;
}

.hero-left{
  float: left;
  /* z-index: 2; */
}

.hero-right{
  float: right;
  /* z-index: 1; */
}

.top{
  z-index: 2;
}

.bottom{
  z-index: 1;
}

.document{

}

.document-title{
  font-size: 38pt;
  font-weight: 400;
}

.document-text{
  margin-top: 32px;
  font-weight: 100;
  font-size: 18pt;
}

.gb-container{
  position: relative;
  margin-top: 16px;
  width: 100%;
  height: 64px;
}

/* .gb-container:active > .gradient-button-shadow{
  filter: blur(4px) brightness(50%);
  transform: translateY(20%) scale(0.8);  
} */

.gradient-button{
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: default;
  position: absolute;
  padding: 8px;
  padding-left: 16px;
  padding-right: 16px;
  text-align: center;
  background: -webkit-linear-gradient(135deg, #F76B1C, #F5515F);
  background: -o-linear-gradient(135deg, #F76B1C, #F5515F);
  background: linear-gradient(-45deg, #F76B1C, #F5515F);
  border-radius: 4px;
  -webkit-transition: -webkit-transform 0.3s;
  transition: -webkit-transform 0.3s;
  -o-transition: transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}

.gradient-button-shadow{
  -webkit-filter: blur(8px) brightness(50%);
          filter: blur(8px) brightness(50%);
  -webkit-transform: translateY(15%) scale(0.9);
      -ms-transform: translateY(15%) scale(0.9);
          transform: translateY(15%) scale(0.9);
}

.gradient-button:active{
  -webkit-transform: scale(0.9);
      -ms-transform: scale(0.9);
          transform: scale(0.9);
  -webkit-transition: -webkit-transform 0.1s;
  transition: -webkit-transform 0.1s;
  -o-transition: transform 0.1s;
  transition: transform 0.1s;
  transition: transform 0.1s, -webkit-transform 0.1s;
}

.altword{
  color: #F5515F;
}

.image{
  min-width: 140%;
  height: 100%;
  background-size: contain;
  left: 0;
  background-repeat: no-repeat;
  background-position: right;
}

.section-container{
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.full-text{
  color: #ffffff;
  font-weight: 600;
  font-size: 18pt;
  float: left;
}

.ghostbutton{
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: default;
  padding: 8px;
  padding-left: 16px;
  padding-right: 16px;
  border-radius: 4px;
  float: left;
  border: 2px solid rgb(255, 255, 255);
  color: #ffffff;
  font-weight: 400;
  -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.25);
          box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.25);
  -webkit-transition: -webkit-box-shadow 0.1s;
  transition: -webkit-box-shadow 0.1s;
  -o-transition: box-shadow 0.1s;
  transition: box-shadow 0.1s;
  transition: box-shadow 0.1s, -webkit-box-shadow 0.1s;
}

.ghostbutton:hover{
  background-color: rgba(255, 255, 255, 0.5);
}

.ghostbutton:active{
  background-color: rgb(255, 255, 255);
  color: #FF9500;
  -webkit-box-shadow: 0 0px 2px 0 rgba(0, 0, 0, 0.25);
          box-shadow: 0 0px 2px 0 rgba(0, 0, 0, 0.25);  
}

.ghostbutton:active > svg > g > path {
  fill: #FF9500 !important;
}

.App-footer{
  width: calc(100% - (16px * 2));
  height: 128px;
  padding: 16px;
  background-color: #ffffff;
  -webkit-box-shadow: inset 0 20px 20px -20px rgb(0, 0, 0);
          box-shadow: inset 0 20px 20px -20px rgb(0, 0, 0);
}

.footer-title{
  color: rgb(211, 211, 211);
  font-size: 24pt;
  font-weight: 200;
  width: 100%;
  text-align: center;
}

.footer-content{
  width: 100%;
  height: 90px;

}

.company-logo{
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 100%;
  height: 90px;
  background-size: auto 256px;
  background-repeat: no-repeat;
  background-position: center;
}

.divider{
  width: 512px;
  height: 2px;
  margin-top: 32px;
  margin-bottom: 8px;
  background-color: rgba(255, 255, 255, 0.25)
}

.light{
  background: #D7EBF0;
}

@-webkit-keyframes float {
	0% {
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
	50% {
		-webkit-transform: translatey(-20px);
		        transform: translatey(-20px);
	}
	100% {
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
}

@keyframes float {
	0% {
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
	50% {
		-webkit-transform: translatey(-20px);
		        transform: translatey(-20px);
	}
	100% {
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
}

@-webkit-keyframes shadow {
	0% {
    -webkit-transform: translate(0, 15%) scale(1);
            transform: translate(0, 15%) scale(1);
    opacity: 1;    
	}
	50% {
    -webkit-transform: translate(0, 15%) scale(1.1);
            transform: translate(0, 15%) scale(1.1);
    opacity: 0.5;
	}
	100% {
    -webkit-transform: translate(0, 15%) scale(1);
            transform: translate(0, 15%) scale(1);
    opacity: 1;
	}
}

@keyframes shadow {
	0% {
    -webkit-transform: translate(0, 15%) scale(1);
            transform: translate(0, 15%) scale(1);
    opacity: 1;    
	}
	50% {
    -webkit-transform: translate(0, 15%) scale(1.1);
            transform: translate(0, 15%) scale(1.1);
    opacity: 0.5;
	}
	100% {
    -webkit-transform: translate(0, 15%) scale(1);
            transform: translate(0, 15%) scale(1);
    opacity: 1;
	}
}

.floating{
  -webkit-transform: translateY(0px);
      -ms-transform: translateY(0px);
          transform: translateY(0px);  
  -webkit-animation: float 6s ease-in-out infinite;  
          animation: float 6s ease-in-out infinite;
}

.scaling{
  -webkit-animation: shadow 6s ease-in-out infinite;
          animation: shadow 6s ease-in-out infinite;  
}


