.full-image{
    width: 100%;
}

.fw-image{
    width: 100%;
    border-radius: 4px;
}

.about-content{
    padding-left: 20vw;
    padding-right: 20vw;    
}

.page-title{
    color: #fff;
    font-size: 28pt;
    padding: 16px;
    font-family: 'Avenir', 'Helvetica', sans-serif;
    font-weight: 600;
}

.person{
    padding: 8px;
    font-family: 'Avenir', 'Helvetica', sans-serif;        
    width: 256px;
    border-radius: 4px;
    background-color: #fff;
    margin: 8px;
    float: left;
}

.person-name{
    margin-top: 8px;
    font-size: 20pt;
    font-weight: 600;  
    color: #2fbeff;  
    text-align: center;    
}

.person-title{
    font-size: 12pt;
    font-weight: 500;
    opacity: 0.5;
    padding-left: 8px;
    text-align: center;
}

.person-description{
    margin-top: 16px;
    font-size: 12pt;
    padding: 8px;
}

.person-container{
    position: relative;
    width: 100%;    
}

.person-container-inner{
    position: absolute;
    width: 125%;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-line-pack: center;
        align-content: center;
    -ms-flex-align: center;
        align-items: center;
    padding: 16px;
}

.person-icon{
    width: 100%;
    height: 48px;
    padding-top: 4px;
    padding-bottom: 4px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: auto 100%;
}

.icon-man{
    background-image: url(assets/person-icons/Man-Filled.svg)
}

.icon-admin{
    background-image: url(assets/person-icons/Admin-Filled.svg)
}

.icon-nerd{
    background-image: url(assets/person-icons/Nerd-Filled.svg)
}

.icon-woman{
    background-image: url(assets/person-icons/Woman-Filled.svg)
}


